import React, { useEffect, useCallback, useState} from 'react';
import RemixPlayer from 'components/remix/RemixPlayer.js';
import RemixList from '../remix/RemixList.js';
import { ConnectButton, lightTheme} from '@rainbow-me/rainbowkit';
import BuyPlayerFlow from 'components/buy/BuyPlayerFlow.js';
import Glossary from './Glossary.js';
import {connect} from 'react-redux';
import SporeEmbed from 'components/player/SporeEmbed.js';
import '../nft-jacker/nft-jacker.scss';
import './drop.scss';

const Drop = ({url, collectionAddress, tokenId, askPrice="2", songTitle, artist, children}) => { 
    const [buying, setBuying] = useState(false);
    const [stems, setStems] = useState(3);
    const [remixToPlay, setRemixToPlay] = useState();
    return (
        <div className="drop nft-jacker jacked">
          {remixToPlay && <div className="remix-backdrop"><RemixPlayer artist={artist} songName={songTitle} setRemix={setRemixToPlay} remix={remixToPlay}/></div>}
          {buying && <BuyPlayerFlow
                       name="SPX-1"
                       askPrice={askPrice} tokenId={1} thumbnail="/inmysoul-cover.jpeg" setBuying={setBuying}/>}
          <div className="connect-button-wrapper">
            <ConnectButton
              accountStatus="address" 
              showBalance={{ smallScreen: false, largeScreen: false }}
              label="Connect Wallet"
            />
          </div>
          <SporeEmbed
            stems={stems}
            isWaveformRainbow={false}
            songTitle={songTitle}
            thumbnail={"/inmysoul-cover.jpeg"}
            artist={artist}
            url={url}
          />
          <div className="drop-description">
            {children}
            <div className="buy-button">
              <div className="action">
                <a href={`https://zora.co/collections/${collectionAddress}/${tokenId}`}>BID ON ZORA ↗</a>
              </div>
              <div className="asking-price">
                <div className="price-label">RESERVE</div>
                <div className="price">{askPrice} ETH</div>
              </div>
            </div>
            <div className="drop-features">
              LISTEN. PLAY. JUICE. PITCH UP. GATE. WOBBLE. STUTTER. LOOP. SPACE. PITCH DOWN. REMIX.
            </div>
            <div className="spore-overview">
              <div className="overview-header">
                SPORE OVERVIEW
              </div>
              <img src="/spore.svg" className="spore-overview-image"/>
            </div>
            <Glossary/>
         </div>
          <RemixList
            remixToPlay={remixToPlay}
            setRemixToPlay={setRemixToPlay}
            collectionAddress={collectionAddress}
            tokenId={tokenId}
            clear={() => []}/>
        </div>);
};

export default Drop;
